* {
  font-family: 'Inter', system-ui;
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 23px;
}

.header {
  display: flex;
  position: fixed;
  flex-direction: column;
  inset: 0 99% 0 0;
  align-items: start;
  background-color: white;
  margin: auto;
  padding: 0;
}

.header-mobile {
  display: flex;
  position: fixed;
  flex-direction: column;
  align-items: start;
  inset: 0 99% 0 0;
  background-color: white;
  margin: auto;
  padding: 0;
}

nav .ul-item {
  display: block;
  margin: 0 auto;
  padding-inline-start: 1em;
}

nav .ul-item .active{
  display: inline-flex;
  align-items: center;
  background-color: #FFE3FF;
  border-radius: 15px;
}
nav .ul-item.full .active{
  display: flex;
  align-items: center;
  background-color: #FFE3FF;
  border-radius: 15px;
}
nav .ul-item .not-active{
  display: flex;
}

nav .ul-item li {
  display: flex;
  align-items: center;
  cursor: pointer;
  padding-block: 0.8rem;
  margin-block: .1rem;
  white-space: nowrap;
}

nav .ul-item li .icon {
  margin-inline: 1rem;
  width: 22px;
  height: 22px;
}

nav .ul-item.icon .show{
  display: none;
}

nav .ul-item li a {
  text-decoration: none;
  color: black;
}

nav .ul-item.full li:hover {
  background-color: #ffe3ff;
  border-radius: 15px;
}

nav .ul-item li:hover {
  transition: .1s ease;
}

nav .ul-item.icon li:hover {
  border-radius: 15px;
  background-color: #ffffff;
  padding-right: 8px;
}

nav .ul-item.icon li.active:hover {
  border-radius: 15px;
  background-color: #ffe3ff;
}

.logoProza{
  width: 50px;
  height: 50px;
}
